// Importați funcția require.context
const importAll = (r) => r.keys().map(r);
// Utilizați require.context pentru a importa toate imaginile din director
const images = importAll(
   require.context(
      '../assets/img/didactic',
      false,
      /\.(jpg|jpeg|png|svg|webp)$/
   )
);
// Exportați matricea cu imagini
export default images;
